import React from 'react';
import PropTypes from 'prop-types';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Header from '@vkontakte/vkui/dist/components/Header/Header';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Tabbar from '@vkontakte/vkui/dist/components/Tabbar/Tabbar';
import TabbarItem from '@vkontakte/vkui/dist/components/TabbarItem/TabbarItem';
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter';

import { Icon20UserCircleOutline } from '@vkontakte/icons';
import { Icon28MenuOutline } from '@vkontakte/icons';
import { Icon28HomeOutline } from '@vkontakte/icons';
import { Icon28GameOutline } from '@vkontakte/icons';

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>Главная</PanelHeader>
		{fetchedUser &&
		<Group header={<Header mode="secondary">User Data Fetched with VK Bridge</Header>}>
			<Cell
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</Cell>
		</Group>}

		<Group header={<Header mode="secondary">Navigation Example</Header>}>
			<Div>
				<Button stretched size="l" mode="secondary" onClick={go} data-to="persik">
					Show me the Persik, please
				</Button>
			</Div>
		</Group>

		<Tabbar>
      		<TabbarItem text="Главная">
        		<Icon28HomeOutline />
      		</TabbarItem>

			<TabbarItem indicator={<Counter size="s" mode="prominent">NEW</Counter>} text="Игры">
				<Icon28GameOutline />
			</TabbarItem>

      		<TabbarItem onClick={id="user"} text="Профиль">
				<Icon20UserCircleOutline />
			</TabbarItem>
	  		<TabbarItem text="Ещё">
				<Icon28MenuOutline />
			</TabbarItem>
    	</Tabbar>

	</Panel>
);

const PageUser = ({ fetchedUser }) => (
	<Panel id="user">
		<PanelHeader>Профиль</PanelHeader>
		{fetchedUser &&
		<Group header={<Header mode="secondary">Ваш профиль</Header>}>
			<Cell
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</Cell>
		</Group>}

		<Group header={<Header mode="secondary">Navigation Example</Header>}>
			<Div>
				<Button stretched size="l" mode="secondary" onClick={go} data-to="persik">
					Show me the Persik, please
				</Button>
			</Div>
		</Group>

		<Tabbar>
      		<TabbarItem text="Главная">
        		<Icon28HomeOutline />
      		</TabbarItem>

			<TabbarItem indicator={<Counter size="s" mode="prominent">NEW</Counter>} text="Игры">
				<Icon28GameOutline />
			</TabbarItem>

      		<TabbarItem text="Профиль">
				<Icon20UserCircleOutline />
			</TabbarItem>
	  		<TabbarItem text="Ещё">
				<Icon28MenuOutline />
			</TabbarItem>
    	</Tabbar>

	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
