import React from 'react';
import PropTypes from 'prop-types';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Tabbar from '@vkontakte/vkui/dist/components/Tabbar/Tabbar';
import TabbarItem from '@vkontakte/vkui/dist/components/TabbarItem/TabbarItem';
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter';

import persik from '../img/persik.png';
import './Persik.css';

import { Icon20UserCircleOutline } from '@vkontakte/icons';
import { Icon28MenuOutline } from '@vkontakte/icons';
import { Icon28HomeOutline } from '@vkontakte/icons';
import { Icon28GameOutline } from '@vkontakte/icons';

const Persik = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="home"/>}
		>
			Persik
		</PanelHeader>
		<img className="Persik" src={persik} alt="Persik The Cat"/>

		<Tabbar>
      		<TabbarItem text="Главная">
        		<Icon28HomeOutline />
      		</TabbarItem>

			<TabbarItem indicator={<Counter size="s" mode="prominent">NEW</Counter>} text="Игры">
				<Icon28GameOutline />
			</TabbarItem>

      		<TabbarItem text="Профиль">
				<Icon20UserCircleOutline />
			</TabbarItem>
	  		<TabbarItem text="Ещё">
				<Icon28MenuOutline />
			</TabbarItem>
    	</Tabbar>

	</Panel>
);

Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Persik;
